"use client";

import Link from "next/link";
import Image from "next/image";
import { usePathname } from "next/navigation";
import { useState } from "react"; // 添加useState
import { Moon, Sun, Search } from "lucide-react"; // 添加图标

export default function DashboardLayout({
  children,
}: {
  children: React.ReactNode;
}) {
  const pathname = usePathname();
  const [darkMode, setDarkMode] = useState(false);
  
  // 切换暗黑模式
  const toggleDarkMode = () => {
    setDarkMode(!darkMode);
    // 这里可以添加实际的dark mode切换逻辑
    document.documentElement.classList.toggle('dark');
  };
  
  const routes = [
    { name: "Dashboard", href: "/dashboard" },
    { name: "Inventory", href: "/inventory" },
    { name: "Store", href: "/store" },
    { name: "Settlement", href: "/settlement" },
  ];

  return (
    <div className="flex h-screen overflow-hidden">
      {/* 侧边栏 */}
      <div className="hidden w-64 flex-col bg-gray-100 dark:bg-gray-800 md:flex border-r">
        <div className="flex h-16 items-center border-b px-4">
          <Link href="/dashboard" className="flex items-center gap-2">
            <Image 
              src="/BW-2.png" 
              alt="CSENTIŌ Logo" 
              width={24} 
              height={24} 
              className="h-6 w-6" 
              onError={(e) => {
                e.currentTarget.src = ""; // 图片加载失败时显示空
                e.currentTarget.style.display = "none";
              }}
            />
            <span className="text-xl font-bold">CSENTIŌ</span>
          </Link>
        </div>
        
        <nav className="flex-1 overflow-auto p-4">
          <ul className="space-y-4">
            {routes.map((route) => (
              <li key={route.href}>
                <Link
                  href={route.href}
                  className={`flex items-center gap-3 rounded-md px-4 py-3 text-base font-medium ${
                    pathname === route.href
                      ? "bg-gray-200 dark:bg-gray-700"
                      : "hover:bg-gray-200 dark:hover:bg-gray-700"
                  }`}
                >
                  {route.name}
                </Link>
              </li>
            ))}
          </ul>
        </nav>
        
        {/* 底部搜索和暗黑模式选项 */}
        <div className="border-t p-4">
          {/* 搜索按钮 */}
          <div className="flex items-center gap-3 rounded-md px-4 py-3 text-base font-medium hover:bg-gray-200 dark:hover:bg-gray-700 mb-2">
            <Search className="h-5 w-5" />
            <span>Search</span>
          </div>
          
          {/* 暗黑模式切换按钮 - 现在与搜索保持相同的对齐和样式 */}
          <button 
            onClick={toggleDarkMode}
            className="flex w-full items-center gap-3 rounded-md px-4 py-3 text-base font-medium hover:bg-gray-200 dark:hover:bg-gray-700"
          >
            {darkMode ? (
              <Sun className="h-5 w-5" />
            ) : (
              <Moon className="h-5 w-5" />
            )}
            <span>Dark Mode</span>
          </button>
        </div>
        
        {/* 用户信息 */}
        <div className="border-t p-4">
          <div className="flex items-center gap-2">
            <div className="flex h-8 w-8 items-center justify-center rounded-full bg-gray-200 dark:bg-gray-700">
              <span className="text-sm font-medium">N</span>
            </div>
            <div className="flex flex-col">
              <span className="text-sm font-medium">CSENTIŌ</span>
              <span className="text-xs text-gray-500">m@csentio.com</span>
            </div>
          </div>
        </div>
      </div>

      {/* 主内容 */}
      <div className="flex flex-1 flex-col overflow-auto">
        {/* 头部 */}
        <header className="border-b">
          <div className="flex h-16 items-center px-4">
            <div className="ml-auto">
              {/* 可以添加头部元素 */}
            </div>
          </div>
        </header>
        
        {/* 页面内容 */}
        <main className="flex-1 p-6">{children}</main>
      </div>
    </div>
  );
}